import React, { Component } from 'react';
import Navbar from './components/Navbar';
import Game from './components/Game';

class App extends Component {

  state = {
    score: 0,
    topScore: 0,
    message: "Click an Image to get started!"
  }

  updateMessage = (message) => {
    this.setState({
      message: message
    })
  }

  increaseScore = () => {
    this.setState({
      score: this.state.score + 1,
    });
    if (this.state.score >= this.state.topScore) {
      this.setState({
        topScore: this.state.topScore + 1
      });
    };
  }

  resetGame = () => {
    this.setState({
      score: 0,
    })
  }

  render() {
    return (
      <div>
        <Navbar message={this.state.message} score={this.state.score} topScore={this.state.topScore} />
        <Game currentMessage={this.state.message} updateMessage={this.updateMessage} resetGame={this.resetGame} increaseScore={this.increaseScore} />
      </div>
    )
  }
}

export default App;



