import React from 'react';

const imageStyle = {
    width: '200px'
}

const columnStyle = {
    margin: '25px 0px'
}

const Image = props => {
    return (
        <div className="col-xs-4 col-sm-3" style={columnStyle}>
            <img onClick={() => props.onClick(props.id)} className="img-thumbnail" style={imageStyle} src={props.src} />
        </div>
    )
}


export default Image;


