import React, { Component } from 'react';
import Image from './Image';
import puppies from '../images/puppies';

class Game extends Component {

    state = {
        images: puppies
    }

    componentDidMount = () => {
        this.shuffleImages();
    }

    shuffleImages = () => {
        let shuffled = this.shuffle(this.state.images);
        this.setState({
            images: shuffled
        });
    }

    shuffle = (a) => {
        for (let i = a.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [a[i], a[j]] = [a[j], a[i]];
        }
        return a;
    }

    handleImageClick = (key) => {

        if (this.props.currentMessage === "Oops! You picked that one already; Game reset.") {
            this.props.updateMessage("Click on another Image!");
        }

        let currentPuppies = this.state.images;

        let puppyIndex = currentPuppies.findIndex(x => x.id === key);

        if (!currentPuppies[puppyIndex].guessed) {

            currentPuppies[puppyIndex].guessed = true;
            this.setState({
                images: currentPuppies
            })
            this.shuffleImages();
            this.props.increaseScore();

        } else {
            currentPuppies.forEach((element, index, arr) => {
                element.guessed = false
            });
            this.props.resetGame();
            this.props.updateMessage("Oops! You picked that one already; Game reset.");
        };

    };

    render() {
        return (
            <div className="container">
            <h2>Click on an image to earn points, but don't click on any more than once!</h2>
                <div className="row">
                    {this.state.images.map((puppy, index) => {
                        return (
                            <Image guessed={puppy.guessed} onClick={this.handleImageClick} key={puppy.id} id={puppy.id} src={puppy.src} />
                        );
                    })}
                </div>
            </div>
        );
    }
}

export default Game;



